/*sign up style*/
.signup {
    width: 400px;
    height: 550px;
    padding-top: 12px;
    position: relative;
    align-items: center;
    justify-items: center;
    padding-left: 35%;
    z-index: 10;
    overflow: hidden;
}

.btN {
    width: 100%;
    background-color: #61C5F7;
    padding: 14px 20px;
    margin: 8px 0;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

.btN a {
    color: white;
}

.logosignup .logo img {
    width: 150px;
    height: 50px;
    padding-left: 44%;
}

.signup .container {
    overflow: hidden;
    width: 100%;
    height: 100%;
    background: #fff;
    border-radius: 10px;

}

.slide-controls {
    position: relative;
    display: flex;
    height: 50px;
    width: 100%;
    overflow: hidden;
    margin: -70px 0 10px 0;
    justify-content: space-between;
    border-radius: 5px;
}

.slide-controls .slide {
    height: 100%;
    width: 100%;
    color: #fff;
    font-size: 18px;
    font-weight: 500;
    text-align: center;
    line-height: 48px;
    cursor: pointer;
    z-index: 1;
    transition: all 0.6s ease;
}

.slide-controls label {
    display: flex;
    padding-left: 15px;
    margin-left: 20px;

}

.slide-controls label.doctor,
.slide-controls label.patiant {
    color: #8A9EAD;
}

.slide-controls .slider-tab {
    position: absolute;
    height: 100%;
    width: 50%;
    left: 0;
    z-index: 0;
    border-radius: 5px;
    background: -webkit-linear-gradient(left, #fff, #fff);
    transition: all 0.6s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

input[type="radio"] {
    display: none;
}

#doctor:checked ~ .slider-tab {
    left: 50%;
    background: -webkit-linear-gradient(left, #61C5F7, #61C5F7);

}

#patiant:checked ~ .slider-tab {
    left: 0%;
    background: -webkit-linear-gradient(left, #61C5F7, #61C5F7);

}

#doctor:checked ~ label.doctor {
    color: #fff;
    cursor: default;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

#doctor:checked ~ label.patiant {
    color: #8A9EAD;
}

#patiant:checked ~ label.doctor {
    color: #8A9EAD;
}

#patiant:checked ~ label.patiant {
    cursor: default;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

